/* src/Navbar.css */

.navbar {
    background-color: #f00606;
    padding: 1rem;
  }
  
  .navbar-list {
    list-style: none;
    display: flex;
    justify-content: space-around;
    margin: 0;
    padding: 0;
  }
  
  .navbar-item {
    color: white;
    cursor: pointer;
    padding: 0.5rem 1rem;
  }
  
  .navbar-item:hover {
    background-color: #3a3f47;
    border-radius: 4px;
  }
  